import React from "react";
import Box from "@material-ui/core/Box";
import {AttachMoney, DeleteSharp} from "@material-ui/icons";
import {Budzet, BudzetComp} from "./BudzetComp";
import {StylesProvider} from "@material-ui/core";
import axios from "axios";
import {DotacjeUEComp} from "./DotacjeUEComp";
import {PieChart, Pie, Cell, Legend} from "recharts";

export interface PanelProps {
    kodGminy: string;
    cytat?: string;
    autor?: string;
    selected?: string[];
    wojewodztwo?: string;
    liczbaDzialow?: string;
}

export interface  DotacjeUE {
    gmina: string;
    gmina_pc: string;
    powiat: string;
    powiat_pc: string;
    wojewodztwo: string;
    wojewodztwo_pc: string;
}

interface Dzial {
    name: string;
    value: number;
}

interface Dane {
    gmina?: string;
    kodGminy?: string;
    dochod_ue?: string;
    dotacje_ue: DotacjeUE;
    budzet: Budzet;
    gospodarka_odpadami_komunalnymi: Budzet;
    ludnosc?: number;
    // udzial_w_dotacjach_ue_powiatu?: string;
    // udzial_w_dotacjach_ue_wojewodztwa?: string;
    wydatki_biezace_ue?: string;
    wydatki_inwestycyjne_ue?: string;
    // bilans:
    // projekty_ue:
    // janosik:
    children: Dzial[];
}

// TODO: Po wyborze gminy tworzyc Panel z propsami
export class Panel extends React.Component<PanelProps, Dane> {
    constructor(props: PanelProps) {
        super(props);
        this.state = {
            budzet: {dochody: "0", wydatki: "0", dochody_kierunek: "-", dochody_zmiana: -1, wydatki_kierunek: "-", wydatki_zmiana: -1},
            gospodarka_odpadami_komunalnymi: {dochody: "0", wydatki: "0", dochody_kierunek: "-", dochody_zmiana: -1, wydatki_kierunek: "-", wydatki_zmiana: -1},
            dotacje_ue: {gmina: "", gmina_pc: "", powiat: "", powiat_pc: "", wojewodztwo: "", wojewodztwo_pc: ""},
            children: [{name: "test", value: 123}],
        };
    }

    renderBudzet(budzet: Budzet) {
        // @ts-ignore
        if (this.props.selected?.indexOf('budzet') > -1) {
            return <React.Fragment>
                <Box className="intro">Budżet za rok <strong>2019</strong></Box>
                <Box display="flex" flexDirection="row">
                    {/*<Money className="icon"></Money>*/}
                    <AttachMoney fontSize="large" className="icon" viewBox=""/>
                    <BudzetComp dane={budzet}/>
                </Box><br/>
            </React.Fragment>
        } else {
            return '';
        }
    }

    renderSmieci(gospodarka_odpadami_komunalnymi: Budzet) {
        // @ts-ignore
        if (this.props.selected?.indexOf('smieci') > -1) {
            return <React.Fragment>
                <Box className="intro">Gospodarka Odpadami Komunalnymi</Box>
                <Box display="flex" flexDirection="row">
                    <DeleteSharp fontSize="large" className="icon" viewBox=""></DeleteSharp>
                    <BudzetComp dane={gospodarka_odpadami_komunalnymi}/>
                </Box><br/>
            </React.Fragment>
        } else {
            return '';
        }
    }

    render() {
        const {gmina, ludnosc, gospodarka_odpadami_komunalnymi, budzet, dotacje_ue, children} = this.state;
        const {cytat, autor, selected, liczbaDzialow} = this.props;
        let dzialy : number = Number.parseInt(liczbaDzialow || "10") || 10;

        const COLORS = ['#0088FE', '#00C49F', '#778888', '#FF8042', "#FFFF33"];

        return (
            <StylesProvider injectFirst>
                <div id="main_section">
                    <Box display="flex" flexDirection="column">
                        <Box className="intro">{gmina} ({ludnosc} mieszkańców)</Box><br/>
                        {cytat !== '' ? <Box className="intro"><i>{cytat}</i></Box> : ''}
                        {autor !== '' ?
                            <Box className="autor">{autor}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Box> : ''}
                        <br/>
                        {this.renderBudzet(budzet)}
                        {this.renderSmieci(gospodarka_odpadami_komunalnymi)}
                        {// @ts-ignore
                         (selected?.indexOf('dotacje_ue') > -1)?
                             <DotacjeUEComp dotacje_ue={dotacje_ue}/>:
                             <React.Fragment></React.Fragment>}
                        {// @ts-ignore
                            (selected?.indexOf('dzialy') > -1)?
                            <PieChart width={550} height={Math.max((Math.min(dzialy, children.length)) * 25, 200)}>
                                <Pie data={children.slice(0, dzialy)} dataKey="value" nameKey="name" cx="50%" cy="50%" innerRadius={20} outerRadius={86} label={false}>
                                    {
                                        children.map((entry, index) => (
                                            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]}/>
                                        ))
                                    }
                                </Pie>
                                {/*<Tooltip />*/}
                                <Legend layout="vertical" verticalAlign="top" align="right" />
                            </PieChart>:
                            <React.Fragment></React.Fragment>}
                    </Box>

                    {/*            <span className="intro" hidden>Dochody z UE: <span id="dochod_ue"*/}
                    {/*                                                               className="pomaranczowy"></span></span><br/>*/}
                    {/*            <span className="intro">Wydatki dofinansowane z UE:<br/>*/}
                    {/*Bieżące gminy: <span id="wydatki_biezace_ue" className="pomaranczowy"></span><br/>*/}
                    {/*Inwestycyjne gminy: <span id="wydatki_inwestycje_ue" className="pomaranczowy"></span><br/>*/}

                    {/*            <span id="janosik:otrzymala" className="intro">Gmina otrzymała <span id="janosik:dochody"*/}
                    {/*                                                                                 className="pomaranczowy"></span>*/}
                    {/*    "janosikowego" od samorządów z wysokimi dochodami</span>*/}
                    {/*            <span id="janosik:wplacila" className="intro">Gmina wpłaciła <span id="janosik:wydatki"*/}
                    {/*                                                                               className="pomaranczowy"></span>*/}
                    {/*    netto "janosikowego" na rzecz samorządów z niskimi dochodami</span><br/><br/><br/>*/}

                </div>
            </StylesProvider>
        );
    }

    common() {
        if (this.props.kodGminy !== this.state.kodGminy) {
            const apiUrl = '/files/' + this.props.kodGminy + '-panel.json';
            axios.get(apiUrl).then((response) => {
                const ret: Dane = response.data;
                this.setState(ret);
            });
            this.setState(Object.assign({}, this.state, {kodGminy: this.props.kodGminy}));
        }
    }

    componentDidMount() {
        this.common();
    }

    componentDidUpdate() {
        this.common();
    }
}